import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			cid: 0, // 文章分类id
			page: 1,
			limit: 10,
			list: [],
			loadStatus: 'more',
			articleType: [],
			typeName: ''
		};
	},
	onLoad(options) {
		this.cid = options.cid;
		this.articleList();
	},
	onReachBottom() {
		if (this.loadStatus === 'more') {
			this.articleList();
		}
	},
	methods: {
		redirect() {
			uni.navigateBack({
				delta: 1
			});
		},
		articleList() {
			let data = {
				page: this.page,
				limit: this.limit,
				type_id: this.cid
			};
			//console.log(this.cid);
			this.loadStatus = 'loading';
			this.$api.articleList(data, res => {
				if (res.status) {
					//console.log(res);
					this.articleType = res.data.article_type;
					uni.setNavigationBarTitle({
						title: res.data.type_name
					});
					const _list = res.data.list;
					this.list = [...this.list, ..._list];
					if (res.data.count > this.list.length) {
						this.loadStatus = 'more';
						this.page++;
					} else {
						// 数据已加载完毕
						this.loadStatus = 'noMore';
					}
				} else {
					// 接口请求出错了
					this.$common.errorToShow(res.msg);
				}
			});
		},
		selectType(id) {
			this.cid = id;
			this.$common.navigateTo('/pages/article/list?cid=' + id);
		},
		// 查看文章详情
		articleDetail(articleId) {
			this.$common.navigateTo('/pages/article/index?id=' + articleId + '&id_type=1');
		}
	}
};
